/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    사용자 유틸리티(css framework)
***************************************************************************/
/* spacing */
$gaps: ".gap", ".row-gap", ".column-gap";

@each $class in $gaps {
  @for $i from 0 through 100 {
    $gap: $i * 0.1;

    &#{$class + "-" + $i} {
      gap: #{$gap}rem !important;
    }
    &#{$class + "-" + $i} {
      gap: #{$gap}rem 0 !important;
    }
    &#{$class + "-" + $i} {
      gap: 0 #{$gap}rem !important;
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/*
  margin

  [html 작성 예]
  <div class="mt-10 mb-10"></div>

  [style 변환시]
  <div style="margin-top:1rem;margin-bottom:1rem"></div>
*/

.m {
  $value: t top, r right, b bottom, l left;

  @each $class, $name in $value {
    @for $i from 0 through 300 {
      $margin: $i * 0.1;

      &#{"-" + $i} {
        margin: #{$margin}rem !important;
      }
      &#{$class + "-" + $i} {
        #{"margin-"+$name}: #{$margin}rem !important;
      }
    }
  }
}
.ml-auto {
  margin-left: auto;
}
.mr-auto {
  margin-right: auto;
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/*
  padding

  [html 작성 예]
  <div class="pt-10 pb-10"></div>

  [style 변환시]
  <div style="padding-top:1rem;padding-bottom:1rem"></div>
*/

.p {
  $value: t top, r right, b bottom, l left;

  @each $class, $name in $value {
    @for $i from 0 through 300 {
      $padding: $i * 0.1;

      &#{"-" + $i} {
        padding: #{$padding}rem !important;
      }
      &#{$class + "-" + $i} {
        #{"padding-"+$name}: #{$padding}rem !important;
      }
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* color */
.color-primary {
  color: $G-color-primary;
}
.color-secondary {
  color: #2db680;
}
.color {
  $value: #fff, #000, #666, #999, #333, #f86666, #eb2020, #171717;

  @each $hexCode in $value {
    $class: str-slice(#{$hexCode}, 2, 7);
    &#{"-" + $class} {
      color: #{$hexCode} !important;
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* Word break */
.word-break {
  word-wrap: break-word !important;
}
.word-keep {
  word-break: keep-all !important;
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* background-color */
.bg-primary {
  background-color: $G-color-primary !important;
}
.bg-secondary {
  background-color: $G-color-secondary !important;
}
.bg-transparent {
  background-color: transparent !important;
}
.bg {
  $value: #fff, #000, #999, #f2f2f2, #fafafa, #cfcfcf, #dedede, #f4f4f4;

  @each $hexCode in $value {
    $class: str-slice(#{$hexCode}, 2, 7);
    &#{"-" + $class} {
      background-color: #{$hexCode} !important;
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* Visually hidden - 접근성 고려하여 시각적으로 숨기면서 화면 리더 사용 가능함 */
.visually-hidden,
.visually-hidden-focusable:not(:focus):not(:focus-within) {
  @include visually-hidden;
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* CSS Reset */
.unset-all {
  all: unset !important;
}
.unset {
  $values: padding, margin, font-size;

  @each $value in $values {
    &#{"-" + $value} {
      #{$value}: unset !important;
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/**
*   clamp (말줄임)
*
*   [html 작성 예]
*   <div class="clamp-1"></div>
*
*/

.clamp {
  @for $i from 0 through 10 {
    &#{"-" + $i} {
      @include clamp($i);
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* hover 상테에서 underline 적용 */

.text-underline-hover {
  text-decoration: none !important;
  @include hover {
    text-decoration: underline !important;
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* 유동적인 Scrolling 영역 */
@mixin scroll-container-common($overflow-x, $overflow-y) {
  position: relative;
  overflow-x: $overflow-x;
  overflow-y: $overflow-y;
  max-width: 100%;
  max-height: 100%;

  // drag 기능막기
  * {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  // 부모 height를 상속받는 경우
  &.position-absolute {
    top: 0;
    left: 0;
  }
  &.translate-middle {
    top: 50%;
    left: 50%;
    &-x {
      left: 50%;
    }
    &-y {
      top: 50%;
    }
  }
}
.scroll-container {
  @include scroll-container-common(auto, auto);
}
.scroll-container-x {
  @include scroll-container-common(auto, hidden);
}
.scroll-container-y {
  @include scroll-container-common(hidden, auto);
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

[data-tooltip-text] {
  $triangleW: 1rem;

  display: block;
  position: relative;
  // cursor: pointer;
  &::before {
    content: "";
    position: absolute;
    bottom: #{$triangleW * 3};
    left: 1.6rem;
    z-index: 11;
    width: $triangleW;
    height: $triangleW;
    border: 1px solid #666;
    border-top: 0;
    border-right: 0;
    background-color: #fff;
    opacity: 0;
    transform: rotate(-45deg) translate(#{$triangleW / -2}, #{$triangleW / 2});
    transform-origin: 25% 50%;
    @include transition;
  }
  &::after {
    content: attr(data-tooltip-text);
    position: absolute;
    bottom: #{$triangleW * 3};
    left: 0;
    z-index: 10;
    padding: 1rem;
    border: 1px solid #666;
    font-size: $G-font-size;
    font-weight: 600;
    color: $G-color-font;
    line-height: 1.6em;
    word-break: keep-all;
    background: #fff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    opacity: 0;
    pointer-events: none;
    @include transition;
  }
  @include hover {
    overflow: visible;
    &::before {
      bottom: #{$triangleW * 2};
      opacity: 1;
    }
    &::after {
      bottom: #{$triangleW * 2};
      opacity: 1;
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

.tooltip {
  $triangleW: 1rem;

  position: relative;
  display: inline-block;
  &-text {
    visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    padding: 1rem;
    border: 1px solid #666;
    font-size: $G-font-size;
    color: $G-color-font;
    line-height: 1.6em;
    word-break: keep-all;
    background: #fff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    opacity: 0;
    transform: translateY(-105%);
    pointer-events: none;
    @include transition($tgs: "opacity, transform");
    &::before {
      content: "";
      position: absolute;
      bottom: -1px;
      left: 1.6rem;
      z-index: 11;
      width: $triangleW;
      height: $triangleW;
      border: 1px solid #666;
      border-top: 0;
      border-right: 0;
      background-color: #fff;
      transform: rotate(-45deg) translate(#{$triangleW / -2}, #{$triangleW / 2});
      transform-origin: 25% 50%;
      pointer-events: none;
    }
    &__title {
      margin-bottom: 0.5rem;
    }
    &__body {
      font-size: fz-in-em(12, $G-font-size);
    }
  }
  &:hover .tooltip-text {
    visibility: visible;
    opacity: 1;
    transform: translateY(-95%);
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* 삼성화재 custom style */
.tooltip-keyword {
  .tooltip-text {
    width: 28rem;
    color: #fff;
    border-radius: $border-radius;
    background-color: #444;
    &::before {
      background-color: #444;
    }
    .keyword-list {
      @include clearBoth;

      font-size: fz-in-em(12, $G-font-size);
      &__title {
        float: left;
        clear: left;
        font-weight: bold;
        &::after {
          content: ":";
          display: inline-block;
          padding: 0 0.3em;
        }
      }
    }
  }
}
