/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    buttons - 버튼 요소
****************************************************************************/
/* size */
$button-font-size: (
  --small-height: fz-in-em(24, 1.2rem),
  --small-padding: fz-in-em(12, 1.2rem),
  --small-font-size: 1.2rem,

  --medium-height: fz-in-em(28, 1.4rem),
  --medium-padding: fz-in-em(12, 1.4rem),
  --medium-font-size: 1.4rem,

  --large-height: fz-in-em(32, 1.4rem),
  --large-padding: fz-in-em(16, 1.4rem),
  --large-font-size: 1.4rem,

  --extra-large-height: fz-in-em(36, 1.4rem),
  --extra-large-padding: fz-in-em(20, 1.4rem),
  --extra-large-font-size: 1.4rem,

  --extra-extra-large-height: fz-in-em(40, 1.4rem),
  --extra-extra-large-padding: fz-in-em(24, 1.4rem),
  --extra-extra-large-font-size: 1.4rem,
);

@mixin button-sizes-common($map, $size) {
  height: map-get($map, --#{$size}-height);
  padding: 0 map-get($map, --#{$size}-padding);
  font-size: map-get($map, --#{$size}-font-size);
}

@mixin button-sizes($map: $button-font-size) {
  @include button-sizes-common($map, "medium");

  &--sm {
    @include button-sizes-common($map, "small");
  }
  &--lg {
    @include button-sizes-common($map, "large");
  }
  &--xl {
    @include button-sizes-common($map, "extra-large");
  }
  &--xxl {
    @include button-sizes-common($map, "extra-extra-large");
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* color */
$button-states: (
  --default-border-color: $G-default-border-color,
  --default-color: $G-default-color,
  --default-icon-color: $G-default-icon-color,
  --default-background-color: $G-default-background-color,

  --hover-border-color: $G-hover-border-color,
  --hover-color: $G-hover-color,
  --hover-icon-color: $G-hover-icon-color,
  --hover-background-color: $G-hover-background-color,

  --active-border-color: $G-active-border-color,
  --active-color: $G-active-color,
  --active-icon-color: $G-active-icon-color,
  --active-background-color: $G-active-background-color,

  --disabled-border-color: $G-disable-border-color,
  --disabled-color: $G-disable-color,
  --disabled-icon-color: $G-disable-icon-color,
  --disabled-background-color: $G-disable-background-color,
);

@mixin button-states-common($map, $state) {
  border: 1px solid map-get($map, --#{$state}-border-color);
  background-color: map-get($map, --#{$state}-background-color);
  .txt {
    color: map-get($map, --#{$state}-color);
  }
  .icon-svg {
    &::after {
      background-color: map-get($map, --#{$state}-icon-color);
    }
  }
}

@mixin button-states($map: $button-states) {
  @include button-states-common($map, "default");

  &:hover {
    @include button-states-common($map, "hover");
  }
  &:active,
  &--is-active {
    @include button-states-common($map, "active");
    &:hover {
      @include button-states-common($map, "active");
    }
  }
  &[disabled] {
    @include button-states-common($map, "disabled");
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* 기본값 */
.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
  z-index: 1;
  @include transition($tgs: "color, border , background-color, background-position");

  &:hover,
  &:active,
  &--is-active {
    z-index: 10;
  }
  &[disabled],
  &[disabled] * {
    pointer-events: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  .txt {
    position: relative;
    z-index: 10;
    font-size: 1em;
    @include transition($tgs: "color, border , background-color, background-position");
  }
  .icon-svg {
    position: relative;
    z-index: 10;
    font-size: 1em;
    &::after {
      @include transition($tgs: "background-color");
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* default style */
.btn-default {
  @include button-sizes;
  @include button-states;
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* theme style */
@mixin theme-states-common($map, $state) {
  border-color: map-get(map-get($map, $state), border-color);
  color: map-get(map-get($map, $state), color);
  background-color: map-get(map-get($map, $state), background-color);

  .txt {
    color: map-get(map-get($map, $state), color);
  }
}

@each $theme, $map in $theme-states {
  .btn-#{$theme} {
    @include button-sizes;
    @include button-states;
    @include theme-states-common($map, "default");

    &:hover {
      @include theme-states-common($map, "hover");
    }
    &:active,
    &--is-active {
      @include theme-states-common($map, "active");
      &:hover {
        @include theme-states-common($map, "active");
      }
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* icon style */
.btn-icon {
  @include button-sizes;
  @include button-states;

  flex: none;
  width: map-get($button-font-size, --medium-height);
  padding: 0 !important;
  &--sm {
    width: map-get($button-font-size, --small-height);
  }
  &--lg {
    width: map-get($button-font-size, --large-height);
  }
}

