/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
  badge - 라벨 컴포넌트
****************************************************************************/
.c-badge {
  $font-size: 1.2rem;

  display: inline-flex;
  align-items: center;
  height: fz-in-em(24, $font-size);
  padding: 0 fz-in-em(10, $font-size);
  border-radius: $border-radius;
  font-size: $font-size;
  color: #fff;
  background-color: $G-color-secondary;
  &:empty {
    display: none;
  }
}
