/* SCSS Document */
@charset "UTF-8";

/* 'AS' 아이콘 mixin */
@mixin as-icon {
    position: absolute;
    display: inline-block;
    content: 'AS';
    color: #fff;
    width: 2.4rem;
    height: 1.6rem;
    background: #171717;
    font-size: 1.2rem;
    font-weight: 700;
    line-height: 1.6rem;
}


/****************************************************************************
  top search - 상단겁색영역
****************************************************************************/
.l-section-top-search {
  $root: &;

  z-index: 10;
  border-bottom: 1px solid $G-default-border-color;
  background-color: #fafafa;

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* l-article */
  @mixin article-common {
    overflow: hidden;
    max-height: 100vh;
    opacity: 1;
    @include transition($time: 0.3, $delay: 0.15, $tgs: "max-height, padding-top, padding-buttom");

    &.l-article {
      &-form {
        padding: $grid-gutter-width $grid-gutter-width $grid-gutter-width 0;
      }
      &-result {
        padding: $grid-gutter-width 0 $grid-gutter-width 0;
        border-left: 1px solid $G-default-border-color;
      }
      &-compact {
        max-height: 0;
        padding: 0;
        opacity: 0;
        transition-duration: 0.2s;
        transition-delay: 0s;
      }
      &:not([class*="compact"]) {
        .l-article-header {
          margin-bottom: 2rem;
          &__title {
            font-size: 1.6rem;
            font-weight: 700;
          }
        }
      }
    }

    &.l-article {
      &-form {
        &-header {
          border: 10px solid blue;
          margin-bottom: 2rem;
          &__title {
            font-size: 1.6rem;
            font-weight: 700;
          }
        }
      }
    }

    // is-compact
    @at-root #{$root}--is-compact .l-article {
      max-height: 0 !important;
      opacity: 0;
      @include transition($time: 0.3, $tgs: "max-height, padding-top, padding-buttom, opacity");

      &-form {
        padding: 0 $grid-gutter-width 0 0 !important;
      }
      &-result {
        padding: 0 !important;
      }
      &-compact {
        max-height: 100vh !important;
        padding: $grid-gutter-width 0 !important;
        opacity: 1 !important;
        transition-delay: 0.25s;
      }
    }
  }

  .l-article-form {
    @include article-common;
  }
  .l-article-result {
    @include article-common;
  }
  .l-article-compact {
    @include article-common;
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* form layout */
  .form-search-list {
    $title-width: 9rem;
    $item-padding: 0.6rem;

    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    &__title {
      display: inline-flex;
      position: relative;
      align-items: center;
      width: $title-width;
      padding: $item-padding 0 $item-padding 1rem;
      &--has-required {
        &::before {
          content: "";
          position: absolute;
          top: 50%;
          left: 0;
          width: 0.4rem;
          height: 0.4rem;
          background-color: $G-color-primary;
          transform: translateY(-50%);
        }
      }
    }
    &__item {
      width: calc(100% - #{$title-width});
      padding: $item-padding 0;

      /* 개별 키워드 상세 페이지 검색어(키워드) 영역 */
      .form-search {
        width: 34.4rem;
        &--has-as {
          #keywordSearch1st,
          #keywordSearch2nd {
            padding-left: 3.6rem;
          }
          &::after {
            @include as-icon;
            text-align: center;
            top: 0.6rem;
            left: 0.8rem;
            z-index: 20;
          }
        }
      }
    }
    /* 개별 키워드 상세 페이지 검색어(키워드) 영역 */
    &__lately-left,
    &__lately-right {
      // position: absolute;
      position: fixed;
      width: 34.4rem;
      background: #fff;
      color: #555;
      border: 1px solid #ededef;
      border-radius: 0 0 0.8rem 0.8rem;
      box-shadow: 0 0.4rem 0.4rem rgba($color: #000000, $alpha: 0.15);
      z-index: 99;
      .lately-left,
      .lately-right {
        &-header {
          height: 3.6rem;
          padding: 0.8rem 1.2rem;
          justify-content: space-between;
          &__title {
            font-size: 1.4rem;
          }
          &__btn {
            font-size: 1.2rem;
          }
        }
        &-body {
          &--list {
            padding: 1.2rem 0;
            &__item {
              display: list-item !important;
              position: relative;
              line-height: 2rem;
              padding: 0 1.2rem 0 4rem;
              &:hover {
                background: #f4f4f4;
              }
              &-contents {
                > span {
                  display: inline-block;
                  text-align: left;
                }
                .txt {
                  width: 8.5rem;
                }
                .type {
                  width: 5.6rem;
                  color: #999;
                  font-size: 1.2rem;
                  &:not(:empty) {
                    &::before {
                      content: '·';
                      margin-right: 0.4rem;
                    }
                  }
                }
                .rule {
                  max-width: 12rem;
                  color: #999;
                  font-size: 1.2rem;
                }

                /* AS라벨 */
                &--has-as {
                  &::before {
                    @include as-icon;   
                    top: 0.2rem;
                    left: 0.8rem;
                  }
                }
              }
              &-del {
                position: absolute;
                top: 0.6rem;
                right: 0.8rem;
                width: 0.9rem;
                height: 0.9rem;
                background-image: icon-svg-bgimg(close, 888) !important;
              }
            }
          }
        }
      }
    }
    .form-search {
      &:not([data-text-value='']) {
        #keywordSearch1st {
          color: #171717;
          background-color: #B193E2;
        }
        #keywordSearch2nd {
          color: #171717;
          background-color: #89DDD8;
        }
      }
    }
  }
&--is-compact .form-search-list__lately-left,
&--is-compact .form-search-list__lately-right {
  display: none;
}
  

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* button - 상단영역축소 */
  .btn-compact {
    $fontSize: 2.3rem;

    position: absolute;
    bottom: -($fontSize / 2);
    left: 50%;
    z-index: 1;
    .icon-svg {
      font-size: 1rem;
      transform: scaleY(-1);
    }

    &.btn-icon--is-active {
      .icon-svg {
        transform: scaleY(1);
      }
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
}
