/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    Frame - 기본구조정의
****************************************************************************/
:root {
  --base-font-size: 10px;
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 10px;
  @include media-breakpoint-down(xxl) {
    font-size: calc(var(--base-font-size) - 1px);
  }
  @include media-breakpoint-up(xxl) {
    font-size: var(--base-font-size);
  }
  @include media-breakpoint-up(qhd) {
    font-size: calc(var(--base-font-size) + 2px);
  }
  @include media-breakpoint-up(thd) {
    font-size: calc(var(--base-font-size) + 4px);
  }
  @include media-breakpoint-up(uhd) {
    font-size: calc(var(--base-font-size) + 10px);
  }
}

#wrap {
  position: relative;
  width: 100%;
  min-width: $G-inner-width;
  font-family: $G-font-family;
  font-size: $G-font-size;
  color: $G-color-font;
  letter-spacing: $G-letter-spacing;

  //확대 축소를 막기
  &[user-scalable='false'],
  &[user-scalable='false'] ~ * * {
    touch-action: pan-y;
  }
}

#header {
  position: relative;
  z-index: 30;
  width: 100%;
  height: $G-header-height;

  // header fixed
  [data-header-fixed='true'] & {
    position: sticky;
    top: 0;
  }

  // header fixed
  &[data-header-fixed='true'] {
    & + #container {
      > .l-section-top-search {
        position: sticky;
        top: $G-header-height;
      }
    }
  }
}

#container {
  position: relative;
  z-index: 20;
  min-height: 100%;
  background-color: #fff;
}

#footer {
  position: relative;
  z-index: 10;
  width: 100%;
  height: 13rem;
}

@mixin inner($width: $G-inner-width) {
  overflow: hidden;
  position: relative;
  z-index: 1;
  width: $width;
  height: 100%;
  margin: 0 auto;
  padding: 0 $G-inner-padding;
  @include clearBoth;
}

.inner {
  @include inner;

  &-fluid {
    @include inner($width: 100%);
  }
}
