/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    Mobile 환경에서 Hover기능 해제
****************************************************************************/
@mixin hover {
  @media (hover: hover) and (pointer: fine) {
    &:hover {
      @content;
    }
  }
  &.is-touched {
    @content;
  }
}

/****************************************************************************
    브라우저 hack
****************************************************************************/
/*
    [style 작성 예]
        .선택자명 {
            @include 지시자명 {
                display:none;
            }
        }
*/

@mixin hack-ie() {
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    @content;
  }
}

/* Safari 10.1+ */
@mixin hack-safari() {
  @media not all and (min-resolution: 0.001dpcm) {
    @supports (-webkit-appearance: none) {
      @content;
    }
  }
}

/****************************************************************************
    Opacity
****************************************************************************/
@mixin opacity($opacity) {
  opacity: $opacity;
  $opacity: $opacity * 100;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=" + $opacity + ")";
}

/****************************************************************************
    Background Alpha(RGBA)
****************************************************************************/
@mixin backgroundAlpha($color, $alpha) {
  $rgba: rgba($color, $alpha);
  $ie-hex-str: ie-hex-str($rgba);
  background-color: transparent;
  background-color: $rgba;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#{$ie-hex-str},endColorstr=#{$ie-hex-str});
  zoom: 1;
}

/****************************************************************************
    Text hover시 Underline( 언더라인 색상 )
****************************************************************************/
@mixin hover-underline($color: #000000, $over_color: #000000, $active_color: #000000, $active: true) {
  > * {
    position: relative;

    &:after {
      content: "";
      position: absolute;
      bottom: -1px;
      left: 50%;
      right: 50%;
      height: 0;
      background: $color;

      @include transition(0.16);
    }
  }

  &:hover {
    > *:after {
      left: 0;
      right: 0;
      height: 1px;
      background: $over_color;
    }
  }

  &.active {
    > *:after {
      @if ($active == true) {
        left: 0;
        right: 0;
        height: 1px;
      }
      background: $active_color;
    }
  }
}

/****************************************************************************
    전체 옵션 트랜지션( 시간 )
****************************************************************************/
@mixin transition($time: 0.18, $delay: 0, $tgs: all, $easing: cubic-bezier(0.5, 0, 0.5, 1), $important: false) {
  $delay: $delay + s;
  $time: $time + s;

  @if ($important == false) {
    $important: null;
  } @else if ($important == true) {
    $important: !important;
  }

  -webkit-transition-property: unquote($tgs) $important;
  -webkit-transition-duration: $time $important;
  -webkit-transition-delay: $delay $important;
  -webkit-transition-timing-function: $easing $important;
  transition-property: unquote($tgs) $important;
  transition-duration: $time $important;
  transition-delay: $delay $important;
  transition-timing-function: $easing $important;
}

/****************************************************************************
    display
****************************************************************************/
@mixin inlineBlock {
  display: inline-block;
}

/****************************************************************************
    Float Clear
****************************************************************************/
@mixin clearBoth {
  &:after {
    content: "";
    display: block;
    clear: both;
  }
}

/****************************************************************************
    SVG Icon 기본설정
****************************************************************************/
@mixin icon-svg-after($icon: check, $color: $G-color-primary) {
  content: "";
  display: inline-flex;
  width: 1em;
  height: 1em;
  background-color: var(--icon-color, $color);
  mask-image: icon-svg-bgimg($icon);
  mask-repeat: no-repeat;
  mask-position: center;
  mask-size: contain;
  -webkit-mask-image: icon-svg-bgimg($icon);
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
  -webkit-mask-size: contain;
}

/****************************************************************************
     clamp (말줄임)
****************************************************************************/
@mixin clamp($lines: 1, $lineHeight: false) {
  display: -webkit-box !important;
  -webkit-line-clamp: $lines !important;
  -webkit-box-orient: vertical !important;
  word-break: break-all !important;
  white-space: normal !important;
  @if ($lineHeight) {
    height: #{$lineHeight * $lines};
    line-height: $lineHeight;
  }
  overflow: hidden !important;
  text-overflow: ellipsis !important;
  &.word-keep {
    word-break: keep-all !important;
  }
}

/****************************************************************************
    Rotate( 각도 )
****************************************************************************/
@mixin rotate($deg) {
  // Rotate
  transform: rotate(#{$deg}deg);
  -webkit-transform: rotate(#{$deg}deg);
  -moz-transform: rotate(#{$deg}deg);
  -o-transform: rotate(#{$deg}deg);
  -ms-transform: rotate(#{$deg}deg);
  filter: progid:DXImageTransform.Microsoft.Matrix(sizingMethod='auto expand', M11=#{cos($deg)}, M12=-#{sin($deg)}, M21=#{sin($deg)}, M22=#{cos($deg)});
  -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(sizingMethod='auto expand', M11=#{cos($deg)}, M12=-#{sin($deg)}, M21=#{sin($deg)}, M22=#{cos($deg)})";
  zoom: 1;
}

/****************************************************************************
    모서리 자르기
****************************************************************************/
@mixin beveled-corners($bg, $tl: 0, $tr: 0, $br: 0, $bl: 0) {
  background: linear-gradient(135deg, transparent $tl, $bg 0) top left, linear-gradient(-135deg, transparent $tr, $bg 0) top right,
    linear-gradient(-45deg, transparent $br, $bg 0) bottom right, linear-gradient(45deg, transparent $bl, $bg 0) bottom left;
  background-size: 51% 55%;
  background-repeat: no-repeat;
}

/****************************************************************************
    그림자( 크기, 투명도, RGB색상 R, RGB색상 G, RGB색상 B, Inner Shadow( "inset" ) )
****************************************************************************/
@mixin shadow($posx: 0, $posy: 0, $blur: 1px, $opacity: 0.05, $color1: 0, $color2: 0, $color3: 0, $inset: "") {
  @if $inset == "" {
    -moz-box-shadow: $posx $posy $blur rgba($color1, $color2, $color3, $opacity);
    -webkit-box-shadow: $posx $posy $blur rgba($color1, $color2, $color3, $opacity);
    box-shadow: $posx $posy $blur rgba($color1, $color2, $color3, $opacity);
  } @else {
    -moz-box-shadow: $inset $posx $posy $blur rgba($color1, $color2, $color3, $opacity);
    -webkit-box-shadow: $inset $posx $posy $blur rgba($color1, $color2, $color3, $opacity);
    box-shadow: $posx $inset $posy $blur rgba($color1, $color2, $color3, $opacity);
  }
}
