@charset "utf-8";

/****************************************************************************
  회원정보 한정 스타일
***************************************************************************/
#container.member {
  display: flex;
  align-items: center;
  min-height: 100vh;
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* Member  */
  .l-member {
    width: 36rem;
    margin: 0 auto;
    &-header {
      &__title {
        color: #171717;
        font-weight: bold;
        line-height: 1;
        letter-spacing: -0.05em;
        &--login {
          margin-bottom: 2rem;
          font-size: 4rem;
        }
        &--pw-set {
          margin-bottom: 3.2rem;
          font-size: 2.8rem;
        }
      }
    }
    .form {
      &-row {
        &.form-text {
          input {
            padding-left: $grid-gutter-width / 2 + 2.4rem;
          }
          &:not([data-text-value=""]) {
            &[data-row="id"] {
              &::before {
                background-image: icon-svg-bgimg(user, 171717) !important;
              }
              .btn-eye {
                background-image: icon-svg-bgimg(eye, 888) !important;
                &--is-active {
                  background-image: icon-svg-bgimg(eye-slash, 888) !important;
                }
              }
            }
            &[data-row="password"],
            &[data-row="새비밀번호"],
            &[data-row="새비밀번호확인"] {
              &::before {
                background-image: icon-svg-bgimg(lock, 171717) !important;
              }
              .btn-eye {
                background-image: icon-svg-bgimg(eye, 888) !important;
                &--is-active {
                  background-image: icon-svg-bgimg(eye-slash, 888) !important;
                }
              }
            }
          }
        }

        // login - id 속성
        &[data-row="id"] {
          &:before {
            content: "";
            position: absolute;
            left: $grid-gutter-width / 2 + 1.2rem;
            top: 50%;
            z-index: 20;
            height: 1.3rem;
            width: 1.3rem;
            background-image: icon-svg-bgimg(user, 999);
            background-position: center center;
            background-size: contain;
            transform: translateY(-50%);
          }
        }

        // login - pw, pw-set 공통 속성
        &[data-row="password"],
        &[data-row="새비밀번호"],
        &[data-row="새비밀번호확인"] {
          input {
            padding-right: 5rem !important;
          }
          &:before {
            content: "";
            position: absolute;
            left: $grid-gutter-width / 2 + 1.2rem;
            top: 50%;
            z-index: 20;
            height: 1.3rem;
            width: 1.3rem;
            background-image: icon-svg-bgimg(lock, 999);
            background-position: center center;
            background-size: contain;
            transform: translateY(-50%);
          }
        }
      }

      // login, pw_set 공통 속성
      .btn-eye {
        position: absolute;
        right: $grid-gutter-width / 2 + 1.2rem;
        top: 50%;
        z-index: 20;
        width: 2.3rem;
        height: 2.3rem;
        background-image: icon-svg-bgimg(eye, cacaca);
        background-position: center center;
        background-size: contain;
        transform: translateY(-50%);
        &--is-active {
          background-image: icon-svg-bgimg(eye-slash, cacaca);
        }
      }
      &__cmt {
        font-size: 1.2rem;
        color: #a7a7a7;
        &--err {
          position: absolute;
          left: 0;
          bottom: 0;
        }
      }
    }
  }
}

