@charset "utf-8";

/***************************Q*************************************************
  Not Found 
****************************************************************************/
#container.notfound {
  .l-section-notfound {
    margin-top: 12rem;
    padding: 0 21.6rem;
    &__header {
      border-bottom: 1px solid #ccc;
      padding-bottom: 3.2rem;
      h3 {
        color: #171717;
        font-size: 3.2rem;
        font-weight: 700;
        line-height: 4.3rem;
        letter-spacing: -0.05em;
        padding-left: 4.4rem;
      }
      .icon-svg-emotion-negative {
        position: absolute;
        font-size: 3.2rem;
        margin-top: 0.8rem;
        &::after {
          background-color: $G-color-primary;
        }
      }
    }
  }
  .l-article-notfound {
    &-header {
      height: auto;
      padding-bottom: 1.9rem;
    }
    &-body {
      margin-top: 2.4rem;
      &__txt {
        color: #555;
        font-size: 1.4rem;
        &:nth-of-type(2) {
          margin-top: 0.4rem;
          margin-bottom: 4.8rem;
        }
      }
    }
  }
}
