/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    table                                  https://getbootstrap.com 참고함
****************************************************************************/
/* 기본 속성 */
@mixin c-table-common {
  $table-font-size: 1.4rem;

  position: relative;
  width: 100%;
  border-bottom: 1px solid $G-default-border-color;
  font-size: $table-font-size;
  color: $G-color-font;
  th,
  td {
    white-space: -moz-pre-wrap !important; /* Mozilla, since 1999 */
    white-space: -webkit-pre-wrap; /* Chrome & Safari */
    white-space: -pre-wrap; /* Opera 4-6 */
    white-space: -o-pre-wrap; /* Opera 7 */
    white-space: pre-wrap; /* CSS3 */
    word-wrap: break-word; /* Internet Explorer 5.5+ */
    word-break: break-all;
    white-space: normal;
  }
  thead {
    tr {
      th {
        position: relative;
        padding: fz-in-em(10, $table-font-size) fz-in-em(5, $table-font-size);

        font-weight: 400;

        &:first-of-type {
          border-left: 0;
        }
        &[colspan] {
          border-bottom: 1px solid #fff;
        }
        .tit {
          padding-right: 1.4rem;
        }
      }
      ~ tr th {
        &:first-of-type {
          // border-left: 1px solid #fff;
        }
      }
    }
    &.is-sticky {
      tr {
        th {
          position: sticky;
          top: 0;
          z-index: 10;
        }
      }
    }
    .form-fieldset {
      display: flex;
      position: absolute;
      top: 0;
      right: 0;
      z-index: 1;
      width: 1.4rem;
      min-width: auto;
      height: 100%;
      .form-check-button {
        position: absolute;
        left: 0;
        width: 100%;
        height: 50%;
        font-size: 0.7rem;
        &[data-row="오름차순"] {
          top: 0;
        }
        &[data-row="내림차순"] {
          bottom: 0;
        }
        label {
          width: 100%;
          padding: 0;
          border: 0;
          border-radius: 0;
        }
      }
    }
  }
  tbody {
    tr {
      th,
      td {
        padding: fz-in-em(10, $table-font-size) fz-in-em(5, $table-font-size);
        border-top: 1px solid $G-default-border-color;
        // border-left: 1px solid $G-default-border-color;
        word-break: keep-all;
        @include transition($tgs: "color, border, background-color");

        &:first-of-type {
          border-left: 0;
        }
      }
      &:first-of-type {
        th,
        td {
          border-top: 0;
        }
      }
    }
  }

  // table Borders
  &[class$="--bordered"] {
    td,
    th {
      border-left: 1px solid $G-default-border-color;
      &:first-of-type {
        border-left: 1px solid $G-default-border-color;
      }
      &:last-of-type {
        border-right: 1px solid $G-default-border-color;
      }
    }
  }
  &--hover {
    tbody {
      tr {
        &:hover {
          th,
          td {
            background-color: $G-hover-background-color;
          }
        }
      }
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* 삼성화재 테이블 커스텀 스타일 */
.c-table {
  @include c-table-common;
  @mixin tr-common {
    height: 4.1rem;
    tr {
      th,
      td {
        &:first-of-type {
          border-left: 1px solid $G-default-border-color;
        }
        &:last-of-type {
          border-right: 1px solid $G-default-border-color;
        }
      }
      .td--is-hover {
        background: #e7e4f1;
      }
      .td--is-active {
        background: $G-color-primary;
        color: #fff;
        .click-menu {
          color: #555;
        }
      }
    }
    .tr--is-hover {
      td {
        background: #e7e4f1;
      }
    }
    .tr--is-active {
      td {
        background: $G-color-primary;
        color: #fff;
        .click-menu {
          color: #555;
        }
      }
    }
  }

  thead {
    @include tr-common;

    tr {
      th,
      td {
        border-left: 1px solid #fff;
        border-top: 1px solid $G-default-border-color !important;
        border-bottom: 1px solid $G-default-border-color !important;
        color: #171717;
        font-weight: 400;
        background: #dedede;
      }
    }
  }
  tbody {
    @include tr-common;

    tr {
      th,
      td {
        border-left: 1px solid $G-default-border-color;
      }
    }
  }

  &--hover-samsung {
    tbody {
      tr:not(.tr--is-active) {
        &:hover {
          th,
          td {
            background-color: #e7e4f1;
          }
        }
      }
    }
  }

  // 건강정보 키워드 - Top30 키워드 순위 변화 테이블
  &--moral__top30-change {
    thead {
      tr {
        th {
          &:first-of-type {
            background: #dedede;
          }
          &:nth-of-type(n + 2):nth-of-type(-n + 5) {
            background: #f4f4f4;
          }
          &:nth-of-type(n + 6),
          &:last-of-type {
            background: #ebebeb;
          }
        }
      }
    }
  }

  // 건강정보 키워드 - 연관어 테이블, 개별 키워드 상세 분석 - 연관어 분석 테이블
  &--moral__related,
  &--keyword__related-left,
  &--keyword__related-right {
    tbody {
      tr {
        td {
          &:nth-of-type(4) {
            padding-right: 1.2rem;
          }
          &:last-of-type() {
            padding-right: 0.8rem;
          }
        }
      }
    }
  }

  // 건강정보 키워드 - Top30 키워드 지역 테이블
  &--moral__top30-area {
    tbody {
      tr {
        td {
          &:nth-of-type(3) {
            padding-right: 0.8rem;
          }
          &:last-of-type() {
            padding-right: 0.8rem;
          }
        }
      }
    }
  }
  /*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/
  /* 개별 키워드 상세 분석 - 연관어 분석 - 좌,우테이블 연관어 하이라이팅 */
  .related--trouble {
    td {
      color: #b88838;
      .click-menu {
        color: #555;
      }
    }
  }
  .related--therapy {
    td {
      color: #985c6d;
      .click-menu {
        color: #555;
      }
    }
  }
  .related--medical {
    td {
      color: #58a277;
      .click-menu {
        color: #555;
      }
    }
  }
  // 개별 키워드 상세 분석 - 연관어 분석 - '공통 연관어만 보기' 켜졌을 때
  &.common-related--active {
    tbody {
      tr {
        td {
          color: $G-color-font !important;
        }
      }
    }
  }

  // 개별 키워드 상세 분석 - 연관 의료기관 테이블
  &--keyword__related-medical {
    tbody {
      tr {
        td {
          &:last-of-type() {
            .icon-svg {
              width: 1.8rem;
              height: 1.8rem;
              &::after {
                width: 100%;
                height: 100%;
                background-color: #888;
              }
              &:hover,
              &:active {
                &::after {
                  background-color: #222;
                }
              }
            }
          }
        }
      }
    }
  }
  // 테이블 내 .percent
  .percent {
    [class^="icon"] {
      position: relative;
      top: -0.1rem;
      margin-right: 0.4rem;
      font-size: 0.8rem;
    }
    &::after {
      content: "%";
    }
  }

  // 테이블 내 증감률 New
  .fluc__new {
    font-size: 1.4rem;
    font-weight: 400;
    color: #f00 !important;
  }

  // 테이블 내 클릭 가능한 요소
  .link {
    cursor: pointer;
    &:hover {
      text-decoration: underline;
    }
  }
  /*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/
  // 테이블에 더보기 버튼 있을 때 스타일
  tbody {
    td {
      .c-table--click-menu {
        position: absolute;
        top: 0.1rem;
        right: 1rem;
      }
    }
  }
  /* 테이블 더보기 메뉴 팝업 */
  .click-menu {
    width: 23.4rem;
    height: 9rem;
    padding: 0.8rem 1.2rem;
    position: fixed;
    top: 3rem;
    left: 0;
    background: #fff;
    border: 1px solid #bdbdbd;
    z-index: 999;
    box-shadow: 0 0.4rem 0.4rem rgba($color: #000000, $alpha: 0.15);
    // visibility: hidden;

    &--anchor {
      a {
        &:hover {
          text-decoration: underline;
        }
      }
      &__medical {
        color: #ccc;
        pointer-events: none;
      }
    }
  }
  .click-menu.is-medical {
    .click-menu--anchor__medical {
      color: #555;
      pointer-events: auto;
    }
  }
  /*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/
  // 테이블 관련정보 팝업 호출하는 span태그 스타일
  .c-table--open__related-pop {
    cursor: pointer;
    &:hover {
      text-decoration: underline;
    }
  }

  /*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/
  // .border-bottom 있는 경우
  @at-root .border-bottom > .c-table {
    border-bottom: 0 !important;
    tbody tr:last-of-type td {
      border-bottom: 0 !important;
    }
  }

  /*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/
  // 테이블 hover 툴팁
  [data-table-type] {
    // 툴팁 스타일
    .c-table--hover-tooltip {
      position: absolute;
      z-index: -1;
      top: -9999px;
      left: -9999px;
      width: 17.8rem;
      height: 6.8rem;
      padding: 0 1.6rem;
      color: #fff;
      background: map-get($theme-colors, "secondary");
      opacity: 0;
      @include transition($tgs: "opacity");

      .tooltip__header {
        margin-top: 1.2rem;
      }
      .tooltip__footer {
        font-size: 1.2rem;
        margin-top: 0.8rem;

        .ft-left {
          float: left;
        }
        .ft-right {
          float: right;
        }

        [class^="icon"] {
          position: relative;
          margin-right: 0.4rem;
          font-size: 0.8rem;
        }
        .percent {
          font-weight: 700;
        }
      }
      // 질병일 경우 스타일
      &.is-trouble {
        width: 28rem;
        height: auto;
        padding-bottom: 1.5rem;
        .tooltip__body {
          margin-top: 0.8rem;
        }
      }

      .tooltip__arrow {
        width: 0;
        height: 0;
        position: absolute;
        border-top: 0.8rem solid rgba($color: #444, $alpha: 0);
        border-left: 0.8rem solid transparent;
        border-right: 0.8rem solid transparent;
        z-index: -2;
        @include transition($tgs: "opacity");
      }
    }
    &:hover {
      .c-table--hover-tooltip {
        opacity: 0.95;
        .tooltip__arrow {
          border-top: 0.8rem solid rgba($color: #444, $alpha: 0.95);
          border-left: 0.8rem solid transparent;
          border-right: 0.8rem solid transparent;
        }
      }
    }
  }
  /*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

  /*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/
