/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* inline icons */
@mixin icon-svg($icon, $color: $G-color-primary) {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 1em;
  height: 1em;
  font-size: var(--font-size, $G-font-size);
  line-height: 1em;
  &::after {
    @include icon-svg-after($icon, $color)
  }
}

.icon-svg {
  $names: close, back, circle, check, calendar, classify, memo, chart-word-cloud, normal-board, triangle-down, triangle-up, search, interj, bar, play, arrow-up,
    bar, user, lock, eye, eye-slash, thumbs-up, fire, chevron-up, chevron-down, chevron-left, chevrons-left, chevron-right, chevrons-right, twitter, facebook,
    emotion-positive, emotion-neutral, emotion-negative, noty-info, noty-success, noty-error, pin, logout, system-monitor, smile, update, warning, download, refresh, chart-candle, chart-line, chart-map,
    click-menu, home-page, normal-medical, dental-clinic, oriental-medical, other-medical;

  @each $name in $names {
    &#{"-" + $name} {
      @include icon-svg($name);
    }
  }
}


/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

.icon-svg-click-menu {
  @include icon-svg("click-menu", #fff);
}
.icon-svg-triangle-up {
  @include icon-svg("triangle-up", #ff0000 );
}
.icon-svg-triangle-down {
  @include icon-svg("triangle-down", #006ecd );
}