/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
  container 전체 검색바
****************************************************************************/
.c-search-bar {
  position: relative;
  z-index: 10;
  height: 6rem;
  background-color: #e5e5e5;
  [data-header-fixed="true"] & {
    position: sticky;
    top: $G-header-height;
  }
  .inner {
    display: flex;
    align-items: center;
  }
  .option {
    flex: none;
    .datepickers {
      display: flex;
      align-items: center;
    }
  }
  .btn-search {
    width: 12rem;
    margin-left: auto;
  }
}
