/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    modal - modal 팝업 요소
****************************************************************************/
@mixin modalFadeIn($translate: top) {
  @if ($translate == "top") {
    $translate: translate(-50%, -10%);
  } @else if ($translate == "bottom") {
    $translate: translate(-50%, -90%);
  } @else if ($translate == "left") {
    $translate: translate(90%, -50%);
  } @else if ($translate == "right") {
    $translate: translate(-190%, -50%);
  }

  &.modal--is-active {
    .modal-wrapper {
      opacity: 1;
      transform: translate(-50%, calc(-50% - 0.5px));
      @include transition($delay: 0.2, $tgs: "opacity, transform");
      @include media-breakpoint-up(qhd) {
        transform: translate(-50%, -50%);
      }
    }
    .modal-dimmed {
      background-color: rgba(0, 0, 0, 0.7);
      @include transition($tgs: "background-color");
    }
  }
  &:not(.modal--is-active) {
    .modal-wrapper {
      opacity: 0;
      transform: $translate;
    }
    .modal-dimmed {
      background-color: rgba(0, 0, 0, 0);
    }
  }
}

@mixin modalFadeOut($transform: bottom) {
  @if ($transform == "top") {
    $transform: translate(-50%, -90%);
  } @else if ($transform == "bottom") {
    $transform: translate(-50%, -10%);
  } @else if ($transform == "left") {
    $transform: translate(-90%, -50%);
  } @else if ($transform == "right") {
    $transform: translate(10%, -50%);
  }

  &.modal--is-disabled {
    .modal-wrapper {
      opacity: 0;
      transform: $transform;
      @include transition($time: 0.3, $tgs: "opacity transform");
    }
    .modal-dimmed {
      background-color: rgba(0, 0, 0, 0);
      @include transition($time: 0.3, $delay: 0.3, $tgs: "background-color");
    }
  }
}

/* 공통 */
.modal {
  overflow: hidden;
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
  font-family: $G-font-family;
  font-size: $G-font-size;
  transform: translate(0, 0);
  &:not(.modal--is-active) {
    z-index: -9999;
    pointer-events: none;
    * {
      pointer-events: none;
    }
  }
}
.modal-wrapper {
  overflow: auto;
  position: absolute;
  z-index: 101;
  top: 50%;
  left: 50%;
  background-color: #fff;
  transform: translate(-50%, -50%);
  &:empty {
    width: 50vw;
    height: 50vw;
    border-radius: 0.5rem;
  }
}
.modal-header {
  position: relative;
  .btn-icon {
    position: absolute;
    z-index: 101;
    top: 50%;
    right: 2.1rem;
    transform: translateY(-50%);
    .icon-svg {
      font-size: 0.6em;
    }
  }
}
.modal-body {
  overflow-y: auto;
  overflow-x: hidden;
  position: relative;
  min-height: 50rem;
  max-height: 85vh;
}
.modal-footer {
  position: relative;
}
.modal-dimmed {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  touch-action: none;
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* 경고 및 확인창 - 공통 */
.modal-alert,
.modal-confirm {
  @include modalFadeIn;
  @include modalFadeOut;

  .modal-wrapper {
    min-width: 36rem;
    max-width: calc(100% - #{$G-inner-padding * 2});
    padding: 3rem;
    border-radius: 1.5rem;
    white-space: nowrap;
  }
  .modal-header {
    &__title {
      display: block;
      margin-bottom: 3.6rem;
      word-break: keep-all;
      line-height: 1.8em;
      strong {
        font-weight: 500;
        white-space: break-spaces;
      }
    }
    &__icon-close {
      display: none;
    }
  }
  .modal-body {
    min-height: auto;
  }
  .modal-footer {
    &__btn-area {
      display: flex;
      justify-content: center;
      margin: 0 -0.4rem;
    }
    .btn {
      margin: 0 0.4rem;
    }
    .btn-cancel .txt:empty::before {
      content: "취소";
    }
    .btn-agree .txt:empty:before {
      content: "확인";
    }
  }
}

/* 확인창 */
.modal-confirm {
  .modal-footer {
    .btn-cancel {
      border: 1px solid $G-default-border-color;
      color: $G-default-color;
      background-color: #fff;
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* 삼성화재 테이블 커스텀 스타일 */

// data-table public style
.modal-data-table {
  @include modalFadeIn;
  @include modalFadeOut;

  .modal-wrapper {
    width: 115.2rem;
    border-radius: 1.5rem;
    @media (max-width: map-get($grid-breakpoints, md)) {
      width: 78.8rem;
    }
  }
  .modal-header {
    display: flex;
    align-items: center;
    height: 6rem;
    background: #f1f1f1;
    padding-left: 2rem;
    border-radius: 1.5rem 1.5rem 0 0;
    font-weight: 600;
    &__title {
      font-size: 1.6rem;
    }
    &__icon-close {
      top: 50%;
      right: 2rem;
      width: 2.8rem;
      height: 2.8rem;
      transform: translateY(-50%);

      &::after {
        width: 0.7rem;
        height: 0.7rem;
        background-color: #888888;
      }
    }
  }
  .modal-body {
    padding: 2rem;
    border-radius: 0 0 1.5rem 1.5rem;

    /* 지역 설정 팝업 */
    #setting-area {
      // width: 25.4rem;
      // height: 27.3rem;
      visibility: hidden;
      position: absolute;
      top: 3rem;
      left: 0;
      z-index: 999;
      padding: 1.6rem;
      border: 1px solid #ededef;
      background: #fff;

      .setting-area {
        &--header {
          &__title {
            font-size: 1.4rem;
            font-weight: 700;
            line-height: 2rem;
          }
        }

        &--body {
          border-bottom: 1px solid #ededef;
          [data-row="전국"] {
            position: absolute;
            top: -3.6rem;
            right: 0;
          }
          .form-fieldset[data-fieldset="지역"] {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            gap: 0.4rem;
            // .form-check-button,
            // .form-check__label {
            //   width: 5.2rem;
            //   height: 2.8rem;
            // }
          }
        }
      }
    }

    .c-table {
      tbody td:first-child {
        cursor: default;
      }

      thead {
        tr {
          th {
            position: relative;
          }
        }
      }
    }

    .c-data-table-total {
      color: #999999;
      font-size: map-get($form-font-size, --small-font-size);

      .count {
        color: #333333;
      }
    }
  }
  .modal-footer {
    padding: 1rem 0 3rem;
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

.modal-data-table {
  // data-table - infos
  &--related {
    .modal-body {
      > .l-article {
        > .l-article-header {
          height: 3.3rem;
          align-items: flex-start;
        }
      }
    }
    .l-article {
      &-header {
        &__title {
          font-size: 1.6rem;
        }
      }
    }
  }

  // data-table - infos
  &--infos {
    .modal-wrapper {
      width: 115.2rem;
    }

    .c-table {
      &--border-top {
        tbody tr {
          > th,
          > td {
            border-top: 1px solid $G-default-border-color;
          }
        }
      }

      &--th-border-bottom-white {
        tbody tr {
          > th {
            border-bottom: 1px solid #ffffff;
          }
        }
      }

      &--pad-left-12 {
        tbody tr {
          > td {
            padding-left: 1.2em;
          }
        }
      }
      &--pad-04 {
        tbody tr {
          > td {
            padding: 0.41429em 0.35714em;
          }
        }
      }

      &--color-555 {
        tbody tr {
          > td {
            color: #555555;
          }
        }
      }
      &--color-666 {
        tbody tr {
          > td {
            color: #666666;
          }
        }
      }
    }

    .icon-svg-home-page {
      font-size: var(--font-size, 1.6rem);
      &:after {
        background-color: var(--icon-color, #888888);
      }
    }
  }

  // data-table - keyword
  &--keyword {
    .modal-wrapper {
      width: 85rem;
    }
  }
  // data-table - ㅑnfo_search
  &--info_search {
    .modal-wrapper {
      width: 100rem;
    }
  }
  // data-table - keyword, info_search
  &--keyword,
  &--info_search {
    .modal-body {
      .c-table {
        tbody tr {
          &.c-table--is-active {
            > td {
              color: #ffffff;
              background-color: $G-color-primary;
            }
          }
        }
      }
    }
    .search-color {
      background: #ffaf38;
    }

    [data-is-before-search="true"] {
      background: #fff url(#{$G-img-path}/common/before-search.png) 0 0 no-repeat !important;
      background-position: 50% 50% !important;
      pointer-events: none;
      > * {
        opacity: 0 !important;
      }
      background-color: #fafafa !important;
    }
  }

  &--infos {
    .modal-wrapper {
      // overflow: unset;
    }
  }

  // data-table - advanced
  &--advanced {
    .modal-body {
      border-radius: 0;
    }
    .modal-wrapper {
      width: 81.6rem;
    }
    .l-article {
      &-header {
        &__title {
          font-size: 1.4rem;
        }
      }
      &-footer {
        border-top: 1px solid $G-color-scrollbar-border;
      }
    }

    .form-fieldset {
      height: 9.4rem;
      color: #555555;
      border: 1px solid $G-color-scrollbar-border;
      padding: 0.8rem 0.4rem;

      &--js-active-input {
        width: 100%;
        height: 100%;
        border: 0;
        padding: 0;
        z-index: 2;
        .form-row {
          width: 100%;
        }
        input {
          width: 100%;
          color: #171717;
          text-align: center;
        }
      }
    }

    .btn-icon {
      .icon-svg {
        &:after {
          // background-color: #888;
          width: 0.9rem;
          height: 0.9rem;
        }
      }
    }

    .danger-color {
      color: #eb6d6d;
    }

    .is-disabled {
      position: relative;

      &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #212121;
        opacity: 0.4;
        z-index: 1;
      }

      .form-fieldset {
        &--js-active-input {
          position: relative;

          &:after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #212121;
            opacity: 0.4;
            z-index: 1;
            pointer-events: none;
          }
        }
      }
    }

    .flex-grow-0 {
      flex-grow: 0;
    }
  }
}
