@charset "UTF-8";
/****************************************************************************
    no Data
****************************************************************************/
[data-is-empty="true"],
.is-nodata {
  background: #fff url(#{$G-img-path}/common/nodata.png) 0 0 no-repeat !important;
  background-position: 50% 50% !important;
  pointer-events: none;
  > * {
    opacity: 0 !important;
  }
  background-color: #fafafa !important;
}
