/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
  section - #container 구성하는 부모와 자식 관계
****************************************************************************/
/* 기본 속성 */
.l-section {
  position: relative;
  &-header {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    position: relative;
    min-width: 100%;
    &__title {
      display: flex;
      align-items: center;
      position: relative;
      font-weight: 600;
      line-height: 1em;
    }
  }
  &-body {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0%;
    position: relative;
    min-width: 100%;
    height: 100%;
  }
  &-footer {
    position: relative;
  }
  > [class^="inner"] {
    display: flex;
    flex-direction: column;
  }
}

/* 삼성화재 - custom style */
.l-section {
  @extend .l-section;
  &-header {
    padding-bottom: 1.2rem;
    border-bottom: 1px solid #dbdbdb;
    &__title {
      font-size: 2rem;
      font-weight: 700;
      padding-left: 2rem;
      &::before {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        top: 50%;
        width: 0.6rem;
        height: 0.6rem;
        background-color: $G-color-primary;
        transform: translateY(-50%) rotate(45deg);
        border-radius: 50%;
      }
    }
  }
  &-body {
    // padding-top: 1.6rem;
  }
}

/****************************************************************************
  article - 기본 콘텐츠 레이아웃
****************************************************************************/
/* 기본 속성 */
.l-article {
  display: flex;
  flex-direction: column;
  height: 100%;
  &-header {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    position: relative;
    width: 100%;
    &__title {
      display: flex;
      align-items: center;
      position: relative;
      line-height: 1em;
      font-weight: 600;
    }
  }
  &-body {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0%;
    position: relative;
    height: 100%;
  }
  &-footer {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0%;
    position: relative;
    height: 100%;
  }
}

/* 삼성화재 - custom style */
.l-article {
  @extend .l-article;
  &-header {
    &__title {
      font-size: 1.8rem;
      font-weight: 700;
    }
    &--has-title {
      margin-bottom: 1.6rem;
      border-bottom: 1px solid #dbdbdb;
      .l-article-header__title {
        margin-bottom: 1.2rem;
      }
    }
  }
}
