/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    cache
****************************************************************************/
$G-cache:                       random(999999999);

/****************************************************************************
    Grid columns
****************************************************************************/
$grid-columns:                  18;
$grid-gutter-width:             2.4rem;

/****************************************************************************
    Breakpoints
****************************************************************************/
$grid-breakpoints: (
  xs:                           0,
  sm:                           576px,
  md:                           768px,
  lg:                           992px,
  xl:                           1200px,
  xxl:                          1400px,
  fhd:                          1920px,
  qhd:                          2048px,
  wqhd:                         2880px,
  uhd:                          3600px,
);

/****************************************************************************
    sizing
****************************************************************************/
/* Layouts */
$G-header-height:               9.6rem;
$G-inner-padding:               3rem;
$G-inner-width:                 148rem + ($G-inner-padding * 2);


/****************************************************************************
    border
****************************************************************************/
/* color */
$border-color:                  #dbdbdb;

/* radius */
$border-radius:                 .4rem !default;

/****************************************************************************
    fonts
****************************************************************************/
$G-font-family:                 "맑은 고딕", "Malgun Gothic", "돋움", "Dotum", "AppleSDGothicNeo-Light", "Sans-serif", "Tahoma";
$G-font-size:                   1.4rem;
$G-letter-spacing:              -0.02em;

/****************************************************************************
    color
****************************************************************************/
/* theme color variables */
$theme-colors: (
  "primary":                    #6B57E3,
  "secondary":                  #444,
  "success":                    #62c796,
  "info":                       #444,
  "warning":                    #FF0000,
  "danger":                     #EB6D6D,
  "light":                      #888,
  "dark":                       #000,  
);

/* $theme-colors UI Components Colors */
$theme-states: (
    primary: (
        default : (
            border-color:       map-get($theme-colors, "primary"),
            color:              #fff,
            background-color:   map-get($theme-colors, "primary"),
        ),
        hover : (
            border-color:       #3B28AB,
            color:              #fff,
            background-color:   #3B28AB,
        ),
        active : (
            border-color:       #19007E,
            color:              #fff,
            background-color:   #19007E,
        ),
    ),
    secondary: (
        default : (
            border-color:       map-get($theme-colors, "secondary"),
            color:              #fff,
            background-color:   map-get($theme-colors, "secondary"),
        ),
        hover : (
            border-color:       #222,
            color:              #fff,
            background-color:   #222,
        ),
        active : (
            border-color:       #222,
            color:              #fff,
            background-color:   #222,
        ),
    )
);

/* System Colors */
$G-color-secondary:             map-get($theme-colors, "secondary");
$G-color-primary:               map-get($theme-colors, "primary");
$G-color-success:               map-get($theme-colors, "success");
$G-color-info:                  map-get($theme-colors, "info");
$G-color-warning:               map-get($theme-colors, "warning");
$G-color-danger:                map-get($theme-colors, "danger");
$G-color-light:                 map-get($theme-colors, "light");
$G-color-dark:                  map-get($theme-colors, "dark");
$G-color-font:                  #333;
$G-color-placeholder:           #ccc;
$G-color-scrollbar:             #ccc;
$G-color-scrollbar-border:      #EDEDEF;

/* UI Components Colors (Icons, Input, Button, Modal, Tabl) */
$G-default-border-color:        $border-color;
$G-default-color:               #7e7e7e;
$G-default-icon-color:          #888;
$G-default-background-color:    #fff;

$G-hover-border-color:          #888;
$G-hover-color:                 $G-default-color;
$G-hover-icon-color:            $G-default-icon-color;
$G-hover-background-color:       #ededef;

$G-focus-border-color:          $G-hover-border-color;
$G-focus-color:                 #fff;
$G-focus-icon-color:            #fff;
$G-focus-background-color:      #888;

$G-active-border-color:         $G-focus-border-color;
$G-active-color:                $G-focus-color;
$G-active-icon-color:           $G-focus-icon-color;
$G-active-background-color:     $G-focus-background-color;

$G-checked-border-color:        $G-focus-border-color;
$G-checked-color:               $G-focus-color;
$G-checked-icon-color:          $G-focus-icon-color;
$G-checked-background-color:    $G-focus-background-color;

$G-disable-border-color:        #ebebeb;
$G-disable-color:               #cacaca;
$G-disable-icon-color:          #cacaca;
$G-disable-background-color:    #ebebeb;

/* linear-gradient */
$G-grad-primary:                linear-gradient(90deg, $G-color-primary 0%, map-get(map-get(map-get($theme-states, primary), active), background-color) 100%);
$G-grad-secondary:              linear-gradient(90deg, $G-color-secondary 0%, map-get(map-get(map-get($theme-states, secondary), active), background-color) 100%);

/****************************************************************************
    shadow
****************************************************************************/
$G-box-shadow:                  0 0 3rem rgba(7, 7, 7, 0.12);
$G-button-shadow:               0 0.3rem 1.7rem rgba(46, 43, 176, 0.4);
$G-text-shadow:                 0 0.05rem 0.2rem rgba(46, 43, 176, 0.6);

/****************************************************************************
    animate
****************************************************************************/
$G-container-delay:             0.25;

/****************************************************************************
    path
****************************************************************************/
$G-img-path:                    "/dashboard/assets/img";
