@charset "utf-8";

#container.expired {
  .l-section-expired {
    margin-top: 12rem;
    padding: 0 21.6rem;
    &__header {
      border-bottom: 1px solid #ccc;
      padding-bottom: 3.2rem;
      h3 {
        color: #171717;
        font-size: 3.2rem;
        font-weight: 700;
        line-height: 4.3rem;
        letter-spacing: -0.05em;
        padding-left: 4.4rem;
      }
      .icon-svg-warning {
        position: absolute;
        font-size: 3.2rem;
        margin-top: 0.8rem;
        &::after {
          background-color: $G-color-primary;
        }
      }
    }
  }
  .l-article-expired {
    &-header {
      height: auto;
      padding-bottom: 1.9rem;
    }
    &-body {
      margin-top: 2.4rem;
      &__txt {
        color: #555;
        font-size: 1.4rem;
        margin-bottom: 4.8rem;
      }
      &__btn {
        font-size: 1.4rem;
        line-height: 1.9rem;
        color: #fff;
        padding: 0.5rem 1.2rem;
        background: $G-color-primary;
        border-radius: 1.4rem;
      }
    }
  }
}
