/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
  페이징
****************************************************************************/
/* size */
@mixin pagination-button-sizes-common($map, $size) {
  width: map-get($map, --#{$size}-height);
}

@mixin pagination-button-sizes($map: $button-font-size) {
  @include pagination-button-sizes-common($map, "medium");

  .btn[class*="--sm"] {
    @include pagination-button-sizes-common($map, "small");
  }
  .btn[class*="--lg"] {
    @include pagination-button-sizes-common($map, "large");
  }
  .btn[class*="--xl"] {
    @include pagination-button-sizes-common($map, "extra-large");
  }
  .btn[class*="--xxl"] {
    @include pagination-button-sizes-common($map, "extra-extra-large");
  }
}

/* 기본 속성 */
@mixin c-pagination-common {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  &__item {
    &--last {
      margin-left: -1px;
    }
  }
  .btn {
    @include pagination-button-sizes;

    padding: 0;
    .icon-svg {
      font-size: 0.8em;
    }
  }
}
/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* 커스텀 스타일 */
.c-pagination {
  @include c-pagination-common;
}
