/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    header
****************************************************************************/
/* header */
#header {
  $headerTopH: 4rem;

  position: sticky;
  top: 0;  
  background: linear-gradient(#fff $headerTopH, $G-color-primary $headerTopH);
  
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* logo */
  .header-logo {
    display: flex;
    align-items: center;
    position: absolute;
    top: 0;
    left: $G-inner-padding;
    height: $headerTopH;
    &__link {
      display: block;
      font-size: 1.2rem;
      font-weight: 700;
      color: $G-color-primary;
    }
    &__img {
      width: auto;
      height: 2.7rem;
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* Indicator  */
  .header-indicator {
    display: flex;
    align-items: center;
    position: absolute;
    top: 0;
    right: $G-inner-padding;
    height: $headerTopH;
    margin: 0 -0.4rem;
    &__item {
      padding:0 0.4rem;
    }
    .icon-svg {
      &-lock {
        margin-right: 0.4rem;
        transform: translateY(1px);        
        &::after {
          background-color: #888;
        }
      }
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* Global Navigation Bar */
  .header-gnb {
    display: flex;
    justify-content: space-around;
    position: absolute;
    top: $headerTopH;
    left: $G-inner-padding;
    right: $G-inner-padding;
    height: ($G-header-height - $headerTopH);
    &__link {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      flex: 1;
      position: relative;
      z-index: 1;
      font-size: 2rem;
      font-weight: 700;
      color: rgba(#fff, 0.7);
      @include transition($tgs: "background");
      
      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 0;
        background-color: rgba(#fff, 0.2);
        @include transition;
      }
      &:hover,
      &--is-active {
        color: #fff;        
        &::after {
          height: 100%;
        }
      }
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* header fixed off/on */
  &[data-header-fixed] {
    .js-header-fixed {
      position: fixed;
      top: $headerTopH / 2;
      left: 50%;
      margin-left: calc(#{$G-inner-width} / 2);
      transform: translateY(-50%);
    }
  }
  &[data-header-fixed="false"] {
    position: relative;
  }
  
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
}
