/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    forms - 폼 요소
****************************************************************************/
/* size */
$form-font-size: (
  --small-height: fz-in-em(18, 1.2rem),
  --small-padding: fz-in-em(12, 1.2rem),
  --small-font-size: 1.2rem,

  --medium-height: fz-in-em(28, 1.4rem),
  --medium-padding: fz-in-em(12, 1.4rem),
  --medium-font-size: 1.4rem,

  --large-height: fz-in-em(32, 1.4rem),
  --large-padding: fz-in-em(16, 1.4rem),
  --large-font-size: 1.4rem,

  --extra-large-height: fz-in-em(36, 1.4rem),
  --extra-large-padding: fz-in-em(20, 1.4rem),
  --extra-large-font-size: 1.4rem,

  --extra-extra-large-height: fz-in-em(40, 1.4rem),
  --extra-extra-large-padding: fz-in-em(24, 1.4rem),
  --extra-extra-large-font-size: 1.4rem,
);

@mixin form-sizes-common($map, $size) {
  height: map-get($map, --#{$size}-height);
  font-size: map-get($map, --#{$size}-font-size);
  input {
    padding: 0 map-get($map, --#{$size}-padding);
  }
  select {
    padding: 0 fz-in-em(30, map-get($map, --#{$size}-font-size)) 0 map-get($map, --#{$size}-padding);
    background-position: right fz-in-em(10, map-get($map, --#{$size}-font-size)) center;
    background-size: fz-in-em(9, map-get($map, --#{$size}-font-size));
  }
  label {
    padding: 0 map-get($map, --#{$size}-padding);
  }
}

@mixin form-sizes($map: $form-font-size) {
  @include form-sizes-common($map, "medium");
  &--sm {
    @include form-sizes-common($map, "small");
  }
  &--lg {
    @include form-sizes-common($map, "large");
  }
  &--xl {
    @include form-sizes-common($map, "extra-large");
  }
  &--xxl {
    @include form-sizes-common($map, "extra-extra-large");
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* color  */
$form-states: (
  --default-border-color: $G-default-border-color,
  --default-color: $G-default-color,
  --default-icon-color: $G-default-icon-color,
  --default-background-color: $G-default-background-color,

  --hover-border-color: $G-hover-border-color,
  --hover-color: $G-hover-color,
  --hover-icon-color: $G-hover-icon-color,
  --hover-background-color: $G-hover-background-color,

  --focus-border-color: $G-focus-border-color,
  --focus-color: $G-focus-color,
  --focus-icon-color: $G-focus-icon-color,
  --focus-background-color: $G-focus-background-color,

  --checked-border-color: $G-checked-border-color,
  --checked-color: $G-checked-color,
  --checked-icon-color: $G-checked-icon-color,
  --checked-background-color: $G-checked-background-color,

  --disabled-border-color: $G-disable-border-color,
  --disabled-color: $G-disable-color,
  --disabled-icon-color: $G-disable-icon-color,
  --disabled-background-color: $G-disable-background-color,
);

/* 공통 interaction */
@mixin form-states-common($map, $state) {
  border: 1px solid var(--color-set, map-get($map, --#{$state}-border-color));

  @if $state == "default" or $state == "hover" {
    background-color: map-get($map, --#{$state}-background-color);
    color: var(--color-set, map-get($map, --#{$state}-color));
    .txt {
      color: var(--color-set, map-get($map, --#{$state}-color)) !important;
    }
    .icon-svg {
      &::after {
        background-color: var(--color-set, map-get($map, --#{$state}-icon-color));
      }
    }
  } @else {
    background-color: var(--color-set, map-get($map, --#{$state}-background-color));
    color: map-get($map, --#{$state}-color);
    .txt {
      color: map-get($map, --#{$state}-color) !important;
    }
    .icon-svg {
      &::after {
        background-color: map-get($map, --#{$state}-icon-color);
      }
    }
  }
}

/* interaction - input */
@mixin form-states-input($map: $form-states) {
  input {
    @include form-states-common($map, "default");

    ~ label {
      .txt {
        color: map-get($map, --default-color);
      }
    }
    &:hover,
    &:focus {
      @include form-states-common($map, "hover");

      ~ label {
        .txt {
          color: map-get($map, --hover-color);
        }
      }
    }
    &:active,
    &:checked {
      @include form-states-common($map, "checked");

      ~ label {
        .txt {
          color: map-get($map, --hover-color);
        }
      }
    }
    &[disabled] {
      @include form-states-common($map, "disabled");

      ~ label {
        .txt {
          color: map-get($map, --disabled-color);
        }
      }
    }
  }
  &--border-bottom {
    input {
      border-top: transparent !important;
      border-left: transparent !important;
      border-right: transparent !important;
    }
  }
}

/* interaction - label */
@mixin form-states-label($map: $form-states) {
  label {
    @include form-states-common($map, "default");
  }
  label:hover {
    @include form-states-common($map, "hover");
  }
  label:active,
  input:checked ~ label {
    @include form-states-common($map, "checked");
  }
  input[disabled] ~ label {
    @include form-states-common($map, "disabled");
  }
}

/* interaction - select */
@mixin form-states-select($map: $form-states) {
  select {
    @include form-states-common($map, "default");

    background-image: icon-svg-bgimg(triangle-down, remove-words(#{map-get($form-states, --default-icon-color)}, ("#")));
    &:hover {
      @include form-states-common($map, "hover");

      background-image: icon-svg-bgimg(triangle-down, remove-words(#{map-get($form-states, --hover-icon-color)}, ("#")));
    }
    &:focus,
    &:active,
    &:checked {
      @include form-states-common($map, "focus");

      background-image: icon-svg-bgimg(triangle-up, remove-words(#{map-get($form-states, --focus-icon-color)}, ("#")));
    }
    &[disabled] {
      @include form-states-common($map, "disabled");

      background-image: icon-svg-bgimg(triangle-down, remove-words(#{map-get($form-states, --disabled-icon-color)}, ("#")));
    }
  }
  &--border-bottom {
    select {
      border-top: transparent !important;
      border-left: transparent !important;
      border-right: transparent !important;
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* layout */
.form {
  position: relative;
}

/* .form-fieldset */
.form-fieldset {
  $f: &;
  position: relative;
  .form-check-button--group {
    #{$f} & {
      border: 10px solid red;
    }
  }
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  &__title {
    color: map-get($form-states, --default-color);
  }
}

/* .form-row[data-row] */
.form-row {
  display: inline-flex;
  flex-grow: 0;
  position: relative;
  z-index: 1;
  select,
  input {
    cursor: pointer;
    @include transition($tgs: "color, border , background-color, background-position");

    &:hover,
    &:focus,
    &:active,
    &:checked {
      z-index: 10;
    }
    &[disabled],
    &[disabled] *,
    &[disabled] ~ label {
      pointer-events: none;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
    }
    ~ label {
      margin-left: 0.5rem;
    }
  }
  label {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    font-size: 1em;
    color: map-get($form-states, --default-icon-color);
    word-break: keep-all;
    @include transition($tgs: "color, border , background-color, background-position");

    &:hover,
    &:active,
    &:checked {
      z-index: 10;
    }
    .txt {
      position: relative;
      z-index: 10;
      font-size: 1em;
      @include transition($tgs: "color, border , background-color, background-position");
    }
    .icon-svg {
      position: relative;
      z-index: 20;
      font-size: 1em;
      &::after {
        @include transition($tgs: "background-color");
      }
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* input 기본값 */
.form-text {
  @include form-sizes;
  @include form-states-input;

  input {
    display: block;
    width: 100%;
    height: 100%;
  }
  &:not([data-text-value=""]) {
    input {
      border-color: map-get($form-states, --focus-border-color);
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* search */
.form-search {
  @include form-sizes;
  @include form-states-input;
  @extend .form-text;

  input {
    &.rounded-pill {
      & ~ .btn-clear,
      & ~ .btn-search {
        border-radius: 100%;
      }
    }
  }
  .btn-clear {
    visibility: hidden;
    position: absolute;
    top: 50%;
    z-index: 20;
    border-color: transparent !important;
    background-color: transparent;
    transform: translateY(-50%);
  }
  .btn-search {
    position: absolute;
    top: 50%;
    z-index: 20;
    transform: translateY(-50%);
  }
  &[data-text-value] {
    .btn-clear {
      visibility: visible;
    }
    &[data-text-value=""] {
      .btn-clear {
        visibility: hidden;
      }
    }
  }

  // size
  $values: "" "medium", "--sm" "small", "--lg" "large", "--xl" "extra-large", "--xxl" "extra-extra-large";

  @each $class, $state in $values {
    &#{$class} {
      input {
        padding-right: map-get($form-font-size, --#{$state}-height) * 2;
      }
      .btn-clear {
        right: map-get($form-font-size, --#{$state}-height);
        height: map-get($form-font-size, --#{$state}-height) * 0.7;
        width: map-get($form-font-size, --#{$state}-height) * 0.7;
        .icon-svg {
          font-size: 1em * 0.7;
        }
      }
      .btn-search {
        right: map-get($form-font-size, --#{$state}-height) * 0.15;
        height: map-get($form-font-size, --#{$state}-height) * 0.7;
        width: map-get($form-font-size, --#{$state}-height) * 0.7;
        .icon-svg {
          font-size: 1em * 0.7;
        }
      }
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* checkbox */
.form-check {
  @include form-sizes;
  @include form-states-input;

  input {
    display: inline-block;
    width: map-get($form-font-size, --medium-height);
    padding: 0;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 50%;
    &:hover {
      background-image: icon-svg-bgimg(check, remove-words(#{map-get($form-states, --hover-icon-color)}, ("#")));
    }
    &:active,
    &:checked {
      background-image: icon-svg-bgimg(check, remove-words(#{map-get($form-states, --checked-icon-color)}, ("#")));
    }
    &[disabled] {
      background-image: none;
    }
  }
  input[type="radio"] {
    border-radius: 100%;
    background-size: 47%;
    &:hover {
      background-image: icon-svg-bgimg(circle, remove-words(#{map-get($form-states, --hover-icon-color)}, ("#")));
    }
    &:checked {
      background-image: icon-svg-bgimg(circle, remove-words(#{map-get($form-states, --checked-icon-color)}, ("#")));
    }
  }
  label {
    padding: 0;
  }
  &--sm {
    input {
      width: map-get($form-font-size, --small-height);
    }
  }
  &--lg {
    input {
      width: map-get($form-font-size, --large-height);
    }
  }
  &--xl {
    input {
      width: map-get($form-font-size, --extra-large-height);
    }
  }
  &--xxl {
    input {
      width: map-get($form-font-size, --extra-extra-large-height);
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* checkbox - switch */
@mixin form-switch-common($name) {
  background-image: icon-svg-bgimg(close, remove-words(#{$G-default-icon-color}, ("#"))),
    icon-svg-bgimg(#{$name}--border, remove-words(#{$G-default-border-color}, ("#")));
  &:hover {
    background-image: icon-svg-bgimg(close, remove-words(#{$G-default-icon-color}, ("#"))), icon-svg-bgimg($name, fff);
  }
  &:checked {
    background-image: icon-svg-bgimg(check, 000), icon-svg-bgimg($name, fff);
    background-position: 76% center, 82% center;
  }
  &[disabled] {
    background-image: icon-svg-bgimg(close, remove-words(#{$G-disable-icon-color}, ("#"))), icon-svg-bgimg($name, fff);
    &:checked {
      background-image: icon-svg-bgimg(check, remove-words(#{$G-disable-icon-color}, ("#"))), icon-svg-bgimg($name, fff);
    }
  }
}

.form-switch {
  @include form-sizes;
  @include form-states-input;

  input {
    width: map-get($form-font-size, --medium-height) * 1.75;
    padding: 0;
    background-position: 24% center, 18% center;
    background-size: 15%, 35%;
    @include form-switch-common("square");

    &.rounded {
      @include form-switch-common("square-rounded");
    }
    &.rounded-pill {
      @include form-switch-common("circle");
    }
  }
  label {
    padding: 0;
  }
  &--sm {
    input {
      width: map-get($form-font-size, --small-height) * 1.75;
    }
  }
  &--lg {
    input {
      width: map-get($form-font-size, --large-height) * 1.75;
    }
  }
  &--xl {
    input {
      width: map-get($form-font-size, --extra-large-height) * 1.75;
    }
  }
  &--xxl {
    input {
      width: map-get($form-font-size, --extra-extra-large-height) * 1.75;
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* checkbox - button */
.form-check-button {
  @include form-sizes;
  @include form-states-label;

  input {
    @include visually-hidden;

    & ~ label {
      margin-left: 0;
    }
  }
  label {
    border-radius: 2em;
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* checkbox - switch-button */
.form-switch-button {
  @include form-sizes;
  @include form-states-label;
  @extend .form-check-button;

  input:checked ~ label {
    border-color: map-get($form-states, --default-border-color);
    background-color: map-get($form-states, --default-background-color);
    &::after {
      opacity: 1;
      border-color: map-get($form-states, --checked-border-color);
      background-color: map-get($form-states, --checked-background-color);
    }
  }
  label {
    border-radius: 0;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 1;
      border: 1px solid map-get($form-states, --default-border-color);
      border-radius: 2em;
      background-color: map-get($form-states, --default-background-color);
      opacity: 0;
      @include transition($tgs: "color, border , background-color, background-position, opacity");
    }
    &:hover {
      border-color: map-get($form-states, --default-border-color);
      background: map-get($form-states, --default-background-color);
      &::after {
        opacity: 1;
        border-color: map-get($form-states, --hover-border-color);
        background-color: map-get($form-states, --hover-background-color);
      }
    }
    &:active {
      border-color: map-get($form-states, --default-border-color);
      background: map-get($form-states, --default-background-color);
      &::after {
        opacity: 1;
        border-color: map-get($form-states, --checked-border-color);
        background-color: map-get($form-states, --checked-background-color);
      }
    }
  }
  &:first-of-type {
    label {
      border-radius: 2em 0 0 2em;
      border-right-width: 0 !important;
    }
  }
  &:nth-of-type(n + 2) {
    label {
      border-left-width: 0 !important;
      border-right-width: 0 !important;
    }
  }
  &:last-of-type {
    label {
      border-radius: 0 2em 2em 0;
      border-right-width: 1px !important;
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* select */
.form-select {
  @include form-sizes;
  @include form-states-select;

  select {
    background-repeat: no-repeat;
  }
  .visually-hidden ~ select {
    width: 100%;
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/
