@charset "UTF-8";

/****************************************************************************
  건강정보 키워드 한정 스타일
****************************************************************************/
#container.moral {
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* 최상단 Tab 스타일 */
  .moral-tabs {
    &-list {
      border-bottom: 1px solid #dbdbdb;
      gap: 0.1rem;
      &__btn {
        font-size: 2rem;
        font-weight: 700;
        width: 14.4rem;
        height: 4.8rem;
        background: #efefef;
        color: #cacaca;
        &:hover {
          background: #a5a5a5;
          color: #cacaca;
        }
        &:active {
          background: #444;
          color: #fff;
        }
        &:first-of-type {
          border-radius: 1.2rem 0 0 0;
        }
        &:last-of-type {
          border-radius: 0 1.2rem 0 0;
        }
      }
    }
    .btn-tab--is-active {
      background: #444;
      color: #fff;
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* 정보량 추이 */
  .l-section[data-section="정보량추이"] {
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* Top30 키워드 순위 변화 */
  .l-section[data-section="Top30키워드순위변화"] {
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* Top30 키워드 분석 */
  .l-section[data-section="Top30키워드분석"] {
    .c-table--moral__related {
      tbody {
        tr {
          td {
            &:hover {
              background: #e7e4f1 !important;
            }
          }
        }
      }
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* 키워드·지역 결합 상위 목록 */
  .l-section[data-section="키워드지역결합상위목록"] {
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  .l-section[data-section="Top30키워드지역"] {
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* 불릿차트 액티브  */
  .chart-bullet-active {
    fill: #6b57e3;
    fill-opacity: 1;
  }
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
  /* 지역 설정 팝업 */
  #setting-area {
    // width: 25.4rem;
    // height: 27.3rem;
    visibility: hidden;
    position: absolute;
    top: 3rem;
    left: 0;
    z-index: 999;
    padding: 1.6rem;
    border: 1px solid #ededef;
    background: #fff;

    .setting-area {
      &--header {
        &__title {
          font-size: 1.4rem;
          font-weight: 700;
          line-height: 2rem;
        }
      }

      &--body {
        border-bottom: 1px solid #ededef;
        [data-row="전국"] {
          position: absolute;
          top: -3.6rem;
          right: 0;
        }
        .form-fieldset[data-fieldset="지역"] {
          display: grid;
          grid-template-columns: 1fr 1fr 1fr 1fr;
          gap: 0.4rem;
          // .form-check-button,
          // .form-check__label {
          //   width: 5.2rem;
          //   height: 2.8rem;
          // }
        }
      }
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* 축 설정 팝업 */
  #setting-coord {
    visibility: hidden;
    // display: flex;
    position: absolute;
    z-index: 999;
    // width: 20.3rem;
    // height: 14rem;
    padding: 1.6rem;
    top: 3rem;
    left: 0;
    border: 1px solid #ededef;
    background: #fff;
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* ??? */
  .ui_positioning_bubble {
    strong {
      display: block;
      margin-top: 0.5rem;
      margin-bottom: 0.5rem;
    }
    .rows {
      font-size: 1.2rem;
      .cnt {
        color: #ffffff;
      }
    }
    .ft_left {
      float: left;
      text-align: left;
      color: #ffffff;
      margin-left: 0.4rem;
      margin-right: 2rem;
    }
    .ft_right {
      float: right;
      text-align: right;
      color: #ffffff;
      font-weight: bold;
      margin-right: 0.4rem;
      [class^="icon"] {
        font-size: 0.8rem;
        margin-right: 0.4rem;
      }
    }
  }

  .amcharts-balloon-div {
    padding: 0 !important;
    margin: 0 !important;

    * {
      padding: 0 !important;
      margin: 0 !important;
    }
  }
  .xy-chart-bubble {
    display: block;

    * {
      padding: 0 !important;
      margin: 0 !important;
    }

    &__title {
      height: 1.2rem;
      font-size: 14px;
    }
    &__txt {
      display: block;
      height: 1.6rem;
      font-size: 12px;
      padding-bottom: 1rem;
    }
    &__row {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;
      margin-top: -1rem !important;
      font-size: 12px;
      vertical-align: top;

      [class^="icon"] {
        font-size: 0.8rem;
        margin-right: 0.4rem;
      }

      &:last-child {
        padding-bottom: 1rem;
      }
    }
    &__num {
      font-weight: bold;
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
}
