@charset "UTF-8";

/****************************************************************************
  의료기관 상세 정보 한정 스타일
****************************************************************************/
#container.medical {
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
  .l-section[data-section="의료기관별상세정보"] {
    .l-article-header__updated-info {
      font-size: 1.2rem;
      line-height: 1.6rem;
      color: $G-default-color;
    }

    .l-article[data-article="의료기관정보"] {
      .medical--information--list {
        height: 17rem;
        background: #f9f9f9;
        &__title {
          width: 39.6rem;
          padding: 2.4rem 2rem 2rem 2rem;
          background: #fff;
          .tit {
            font-size: 1.4rem;
          }
          .txt {
            font-size: 2.2rem;
            line-height: 3rem;
          }
          .since {
            color: $G-default-color;
          }
        }
        &__item {
          width: 54.5rem;
          margin: 1.2rem 0;
          padding: 1.2rem 2.4rem;
          .tit,
          .txt {
            display: inline-block;
            height: 4rem;
            line-height: 2rem;
            overflow: hidden;
          }

          &:first-of-type {
            .tit {
              width: 5.5rem;
              margin-right: 1.6rem;
              vertical-align: top;
            }
            .txt {
              width: 42rem;
            }
          }
          &:last-of-type {
            .tit {
              width: 13.7rem;
              margin-right: 1.6rem;
            }
            .txt {

            }
          }

          .icon-svg {
            width: 1.6rem;
            height: 1.6rem;
            &::after {
              width: 100%;
              height: 100%;
              background-color: #888;
            }
            &:hover,
            &:active {
              &::after {
                background-color: #222;
              }
            }
          }
        }
      }
    }

    .l-article[data-article="진료정보"] {
      .c-table--medical__treatment-info {
        tbody {
          td {
            &:last-of-type() {
              background: #f9f9f9;
            }
          }
        }
      }

      .treatment-time--list {
        li {
          height: 4rem;
          display: flex;
          align-items: center;
          border: 1px solid $border-color;
          border-top: none;
        }
        &__title {
          background: #dedede;
        }
        &__item {
          .day {
            position: absolute;
            top: inherit;
            left: 2rem;
          }
          .time {
            width: 100%;
          }
        }
      }

      .lunch-time--list,
      .close-medical--list {
        &__title {
          width: 100%;
          height: 4rem;
          display: flex;
          align-items: center;
          justify-content: center;
          background: #dedede;
        }
        li {
          height: 6rem;
          border: 1px solid $border-color;
          border-top: none;
        }
        .lunch--list,
        .close--list {
          height: 6rem;
          &__title {
            width: 8.2rem;
            display: flex;
            justify-content: center;
            align-items: center;
            border-right: 1px solid $border-color;
          }
          &__item {
            display: flex;
            justify-content: center;
            align-items: center;
            padding-left: 1.2rem;
          }
        }
      }
    }

    .l-article[data-article="인력현황"] {
      .human-present--list {
        dl {
          height: 8rem;
          background: #f9f9f9;
          dt {
            width: 39.5rem;
            padding: 0 2.8rem 0 1.6rem;
            align-items: center;
            background: #fff;
            border-right: 1px solid $border-color;
            .tit {
              font-size: 2rem;
            }
            .count {
              font-size: 2.2rem;
              color: #171717;
            }
          }
          dd {
            flex: auto;
            margin: 0.8rem 0;
            .tit {
              margin-top: 0.4rem;
            }
            .count {
              font-size: 1.8rem;
              margin-top: 1.2rem;
            }
            &:not(:last-of-type) {
              border-right: 1px solid $border-color;
            }
          }
        }
        .icon-svg {
          width: 5rem;
          height: 5rem;
          border-radius: 50%;
          background: #f3f3f3;
          &-normal-medical {
            &::after {
              width: 2.2rem;
              height: 2.8rem;
              background-color: #ccc;
            }
          }
          &-dental-clinic {
            &::after {
              width: 2.2rem;
              height: 2.6rem;
              background-color: #ccc;
            }
          }
          &-oriental-medical {
            &::after {
              width: 3.4rem;
              height: 2.5rem;
              background-color: #ccc;
            }
          }
          &-other-medical {
            &::after {
              width: 2.4rem;
              height: 2.2rem;
              background-color: #ccc;
            }
          }
        }
      }
    }

    .l-article[data-article="병상현황"] {
      .c-table--medical__normal-bed {
        tbody {
          tr {
            &:first-of-type() {
              td {
                background: #f9f9f9;
              }
            }
          }
        }
      }
    }
  }
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
  .l-section[data-section="정보량추이"] {
  }
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
  .l-section[data-section="Top30키워드순위변화"] {
  }
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
  .l-section[data-section="게시글목록"] {
  }
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
  /* 키워드 상세 설정 팝업 */
  #setting-keyword-detail {
    // visibility: hidden;
    // display: flex;
    position: absolute;
    z-index: 999;
    // width: 20.3rem;
    // height: 14rem;
    padding: 1.6rem;
    top: 3rem;
    left: 0;
    border: 1px solid #ededef;
    background: #fff;
    box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.15);
  }

    /* 키워드 상세 설정 팝업 */
    #setting-article-filter {
      // visibility: hidden;
      // display: flex;
      position: absolute;
      z-index: 999;
      // width: 20.3rem;
      // height: 14rem;
      padding: 1.6rem;
      top: 3rem;
      left: 0;
      border: 1px solid #ededef;
      background: #fff;
      box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.15);
    }
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
}